import React from 'react'
import RoleService from '../Service/RoleService'

class authListForRole extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            id:this.props.match.params.id,
            role:{},
            auths:[]
        }
    }
    componentDidMount(){
        RoleService.findById(this.state.id).then(res=>{
            this.setState({role:res.data});
        });
        RoleService.authListForRole(this.state.id).then(res=>{
            this.setState({auths:res.data});
        });
    }

    render(){
       return(
        <div style={{marginTop:"2.5%"}}>
            <button className="btn btn-sm text-white font-weight-bold" style={{backgroundColor:"purple",marginLeft:"28.5%"}} onClick={()=>this.props.history.goBack()}>返回</button>
            <div className="mx-auto card" style={{marginTop:"2.5%",width:"30rem"}}>
            <h5 className="card-header font-weight-bold text-secondary text-center">{this.state.role.rolename}</h5>
            <div className="card-body">
            {
                this.state.auths.map(auth=>{
                    return(
                    <div>{auth.resname}:    {auth.description}</div>
                    )
                })
            }
            </div>
        </div>
        </div>
       )
    }
}

export default authListForRole 